body {
  background-color: #333;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

header {
  color: #fff;
  text-align: center;
  background-color: #333;
  padding: 20px 0;
}

header h1 {
  color: #fff;
  margin: 0;
  font-size: 2em;
}

header nav ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

header nav ul a {
  color: #fff;
  margin: 0 10px;
  text-decoration: none;
}

.product {
  background-color: gray;
  border-radius: 30px;
  margin: 20px;
  padding: 20px;
  box-shadow: 0 0 10px #0000001a;
}

.product h2 {
  text-align: center;
  size: 100px;
  color: #000;
  margin-top: 30px;
  font-size: 1.5em;
}

.product p {
  color: #000;
}

.product button {
  color: gray;
  cursor: pointer;
  background-color: #333;
  border: none;
  border-radius: 5px;
  margin-left: 50px;
  padding: 10px 20px;
  transition: background-color .3s;
}

.product button:hover {
  background-color: #1a1a1a;
}

footer {
  color: gray;
  text-align: center;
  background-color: #333;
  padding: 20px 0;
}

